import React from 'react';
import {connect} from 'react-redux';

import {goBack} from "../store/router/actions";

import {
    Panel,
    PanelHeader,
    PanelHeaderBack,
    Group,
    Placeholder,
    Button,
    MiniInfoCell
} from "@vkontakte/vkui";

import {
    Icon28DonateCircleFillYellow,
    Icon20LifebuoyOutline,
    Icon20GiftOutline,
    Icon20ServicesOutline,
    Icon20FavoriteOutline
} from "@vkontakte/icons";

class Donate extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            snackbar: null,
            loader: false,
        };
    }

    render() {
        const {id, goBack} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader
                    left={<PanelHeaderBack onClick={() => goBack()}/>}
                >
                    VK Donut
                </PanelHeader>

                <Group>
                    <Placeholder
                    icon={<Icon28DonateCircleFillYellow width={56} height={56} />}
                    stretched
                    header="   VK Donut?"
                    action={<Button mode="commerce" size="m" href="https://vk.com/donut/teamskt" target="_blank"> </Button>}
                >
                        <MiniInfoCell
                        before={<Icon20LifebuoyOutline />}
                        textWrap="full"
                        >
                                 .
                        </MiniInfoCell>

                        <MiniInfoCell
                        before={<Icon20GiftOutline />}
                        textWrap="full"
                        >
                                VK Donut.
                        </MiniInfoCell>

                        <MiniInfoCell
                        before={<Icon20ServicesOutline />}
                        textWrap="full"
                        >
                              Premium   .
                        </MiniInfoCell>

                        <MiniInfoCell
                        before={<Icon20FavoriteOutline />}
                        textWrap="full"
                        >
                                  .
                        </MiniInfoCell>
                    </Placeholder>
                </Group>

            </Panel>
        );
    }

}

const mapDispatchToProps = {
    goBack
};

export default connect(null, mapDispatchToProps)(Donate);