import React from 'react';
import {connect} from 'react-redux';

import {
    ModalPage, 
    ModalPageHeader, 
    PanelHeaderButton, 
    withPlatform, 
    IOS, 
    Div,
    Group,
    SimpleCell,
    Card,
    Placeholder
} from "@vkontakte/vkui";

import { 
    Icon24Dismiss,
    Icon24Cancel,
    Icon28PaintRollerOutline,
    Icon28SmileOutline,
    Icon28CalendarOutline,
    Icon28BookOutline,
    Icon28DonateOutline,
    Icon28UserStarBadgeOutline,
    Icon28VideoOutline,
    Icon28SparkleOutline,
    Icon28FireOutline,
    Icon28BugOutline
} from '@vkontakte/icons';

import Memoji from "../../../img/memoji.png";

import queryString from 'query-string';

class UpdateInfoModal extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            device: ''
        };

    }

    componentDidMount() {
        var params = queryString.parse(window.location.search);
        this.setState({
            device: params.vk_platform
        });
    }

    render() {
        const {id, onClose, platform} = this.props;

        return (
            <ModalPage
                id={id}
                header={
                    <ModalPageHeader
                        left={platform !== IOS &&
                        <PanelHeaderButton onClick={onClose}><Icon24Cancel/></PanelHeaderButton>}
                        right={platform === IOS &&
                        <PanelHeaderButton onClick={onClose}><Icon24Dismiss/></PanelHeaderButton>}
                    >
                        
                    </ModalPageHeader>
                }
                onClose={() => {
                    onClose();
                    //document.body.style.overflow = 'auto';
                }}
                settlingHeight={100}
            >
                <Div>
                    <Placeholder
                            icon={<img src={Memoji} height="150" width="150" alt="memoji" />}
                            header=" !"
                            >
                                SKT Go . ,  .  !
                        </Placeholder>
                    <Group>
                        <Card>
                            <SimpleCell
                            disabled
                            description="   "
                            before={<Icon28PaintRollerOutline/>} 
                            >
                                 
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="   "
                            before={<Icon28SmileOutline/>} 
                            >
                                  
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="    "
                            before={<Icon28CalendarOutline/>} 
                            >
                                  
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="   "
                            before={<Icon28BookOutline/>} 
                            >
                                 
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="  "
                            before={<Icon28DonateOutline/>} 
                            >
                                 
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="   "
                            before={<Icon28UserStarBadgeOutline/>} 
                            >
                                 
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description=" "
                            before={<Icon28VideoOutline/>} 
                            >
                                  
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="   "
                            before={<Icon28SparkleOutline/>} 
                            >
                                 
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="   "
                            before={<Icon28FireOutline/>} 
                            >
                                 
                            </SimpleCell>
                        </Card>

                        <Card style={{ marginTop: "10px", marginBottom: "10px"}}>
                            <SimpleCell
                            disabled
                            description="     "
                            before={<Icon28BugOutline/>} 
                            >
                                 
                            </SimpleCell>
                        </Card>
                    </Group>
                    {this.state.device !== 'mobile_iphone' && <div style={{ width: '100%', textAlign: 'center', marginTop: 8 }}>
                    </div>}
                </Div>
            </ModalPage>
        );
    }

}

export default withPlatform(connect()(UpdateInfoModal));
